er {\n  text-align: center;\n  position: absolute;\n  top: 40%;\n  left: 15%;\n  margin: 0 auto;\n  width: 1000px;\n  color: #fefae0;\n}\n.hero__title {\n  font-weight: 300px;\n  font-size: 36px;\n}\n.hero__tagline {\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 300px;\n}\n/* Main */\nmain {\n  /* width: max-content;\n  max-width: 100%; */\n  width: 100%;\n  margin: 0 auto;\n  box-sizing: border-box;\n}\n.headline h4:hover {\n  text-decoration-line: underline;\n  text-decoration-color: #d5bdaf;\n  text-decoration-thickness: 3px;\n}\n\n/* restaurant */\n.restaurant {\n  display: grid;\n  grid-template-columns: repeat(3,1fr);\n  gap: 10px;\n  text-align: left;\n  /* max-width:100%; */\n  margin: 20px 20px;\n}\n.restaurant__content a {\n  text-decoration: none;\n  color: black;\n}\n.restaurant__content a:hover{\n  color: #edede9;\n}\n.restaurant__item{\n  /* width:350px; */\n  box-shadow: 4px 4px 0 #e3d5ca;\n  background-color: #d5bdaf;\n  border-radius: 5px;\n  margin-top:15px;\n  padding: 16px;\n}\n.restaurant__item{\n  padding:16px;\n}\n.restaurant__image {\n  width:100%;\n}\n.restaurant__name {\n  font-size: 25px;\n}\n.restaurant__description {\n  text-align: justify;\n}\n@media screen and (min-width:300px){\n  .headline h4 {\n    font-size: 25px;\n    text-align: center;\n  }\n  .restaurant {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 10px;\n    margin: 20px 30px;\n    max-width:100%;\n  }\n  .restaurant__item{\n    width: 250px ;\n  }\n  .detail_head h4{\n    font-size: 25px;\n    text-align: center;\n  }\n  .detail_container{\n    margin: 20px auto;\n    font-weight: 300;\n    max-width: 90%;\n    background-color: #d5bdaf;\n  }\n  .detail_content img{\n    width: 90%;\n    margin: 10px auto;\n    display: block;\n  }\n  .detail_container .detail_content p{\n    font-size: 12px;\n  }\n  .detail_container .detail_content h5{\n    font-size: 20px;\n  }\n  .detail_container .detail_content h6{\n    font-size: 20px;\n    text-align: ce