%;\n    margin:20px 40px;\n  }\n  .favorite_container h4{\n    font-size: 36px;\n  }\n}\n\n/* Detail Restaurant */\n.detail_container .detail_content{\n  font-weight: 300;\n  margin: 20px 20px;\n}\n.detail_container .detail_content h5{\n  text-align: center;\n}\n.detail_head h4:hover {\n  text-decoration-line: underline;\n  text-decoration-color: #d5bdaf;\n  text-decoration-thickness: 3px;\n}\n.restaurant__menu-list{\n  margin: 10px 20px;\n}\n.favorite {\n  font-size: 18px;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  background-color: #d6ccc2;\n  color: white;\n  border: 0;\n  border-radius: 50%;\n  width: 55px;\n  height: 55px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.review_header {\n  margin-bottom: 5px;\n}\n/* Favorite Restaurant */\n.favorite_container h4 {\n  text-align: center;\n  margin: 20px auto;\n  /* font-size: 36px; */\n}\n.favorite_container h4:hover {\n  text-decoration-line: underline;\n  text-decoration-color:#d5bdaf;\n  text-decoration-thickness: 3px;\n}\n/* .favorite_container .favorite_content{\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 20px;\n  width:95%;\n  margin:20px 40px;\n} */\n/* footer */\nfooter {\n  background-color: #d6ccc2;\n  text-align: center;\n  font-size: 20px;\n  font-family: \"Nerko One\", cursive;\n  margin-top: 20px;\n}\n\n/*\nResponsive\n*/\n\n/* mobile*/\n@media screen and (max-width: 600px) {\n  .nav__list .nav__item.drawer {\n    display: block;\n  }\n  .nav__list .nav__item h1 {\n    display: block;\n    text-decoration: none;\n  }\n  .nav__list .nav__item.menu {\n    display: none;\n  }\n  .nav__list .nav__item.menu.open {\n    display: block;\n    left: 0;\n  }\n  .nav__list .nav__item.menu.open a {\n    min-width: 44px;\n    min-height: 44px;\n  }\n  .nav__list .nav__item a {\n    display: block;\n    padding: 10px;\n    margin-left: 30px;\n  }\n  .nav__list .nav__item a:hover {\n    background-color: #edede9;\n    color: blac